import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import MainHeader from "../../components/Users/MainHeader";
import "bootstrap/dist/css/bootstrap.min.css";

import {
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import moment from "moment";
import { momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import { format, toZonedTime } from "date-fns-tz";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";
export default function Subscription() {
  const localizer = momentLocalizer(moment);
  const [plans, setplans] = useState([]);
  const [selectedPeriod, setSelectedPeriod] = useState("monthly"); // default monthly
  const [showPopup, setShowPopup] = useState(false);
  const [planId, setplanId] = useState("");
  const apiUrl = "https://blueprintcatalyst.com/api/user/";
  const storedUsername = localStorage.getItem("UserLoginData");
  const userLogin = JSON.parse(storedUsername);
  useEffect(() => {
    getallSubscriptionPlan();
  }, []);

  const [userPlans, setUserPlans] = useState([]);

  const getallSubscriptionPlan = async () => {
    try {
      const res = await axios.post(apiUrl + "getusersSubscriptionPlan", {
        user_id: userLogin.id,
      });

      const data = res.data.results;

      if (!data) {
        console.error("No subscription data received");
        return;
      }

      const plans = [];

      if (data.dataroomOneTime) {
        plans.push({
          name: "Dataroom One-Time",
          status: data.dataroomOneTime.status || "N/A",
          price: `â‚¬${data.dataroomOneTime.price}`,
          renewalDate:
            formatCurrentDate(data.investorReporting.end_date) || "N/A",
          lastPayment:
            formatCurrentDate(data.dataroomOneTime.start_date) || "N/A",
          features: ["One-time access", "Secure dataroom", "No renewal"],
        });
      }

      if (data.investorReporting) {
        plans.push({
          name: "Investor Reporting",
          status: data.investorReporting.status || "N/A",
          price: `â‚¬${data.investorReporting.price}`,
          renewalDate:
            formatCurrentDate(data.investorReporting.end_date) || "N/A",
          lastPayment:
            formatCurrentDate(data.investorReporting.start_date) || "N/A",
          features: [
            "Monthly investor updates",
            "Download reports",
            "Analytics dashboard",
          ],
        });
      }

      if (data.perInstancePurchases?.length > 0) {
        data.perInstancePurchases.forEach((instance, i) => {
          plans.push({
            name: `Per Instance #${i + 1}`,
            status: instance.payment_status || "N/A",
            price: `â‚¬${instance.price}`,
            lastPayment: formatCurrentDate(instance.created_at) || "N/A",
            features: ["One-time report", "Single-use instance"],
          });
        });
      }
      console.log(data.academySubscription);
      if (data.academySubscription) {
        plans.push({
          name: "International Entrepreneur Academy",
          status: data.academySubscription.status || "N/A",
          price: `â‚¬${data.academySubscription.price || 0}`,
          lastPayment:
            formatCurrentDate(data.academySubscription.created_at) || "N/A",
          features: [
            "Access to all modules",
            "One-time payment",
            "Global entrepreneurship insights",
          ],
        });
      }

      setUserPlans(plans);
    } catch (err) {
      console.error("Error fetching subscription plans:", err);
    }
  };
  function formatCurrentDate(input) {
    const date = new Date(input);

    if (isNaN(date)) return "";
    const months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    const day = date.getDate();
    const month = months[date.getMonth()];
    const year = date.getFullYear();

    const getOrdinal = (n) => {
      if (n >= 11 && n <= 13) return "th";
      switch (n % 10) {
        case 1:
          return "st";
        case 2:
          return "nd";
        case 3:
          return "rd";
        default:
          return "th";
      }
    };

    return `${month} ${day}${getOrdinal(day)}, ${year}`;
  }
  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <MainHeader />

          <SectionWrapper className="d-block py-5">
            <div className="container-lg">
              <div className="row justify-content-center">
                <div className="col-md-3">
                  <ModuleSideNav />
                </div>

                <div className="col-md-9">
                  <h2 className="mb-4">Your Subscriptions</h2>

                  {userPlans.map((plan, index) => {
                    // âœ… Determine status based on end date
                    const cleanDateStr = plan.renewalDate
                      ? plan.renewalDate.replace(/(\d+)(st|nd|rd|th)/, "$1")
                      : null;

                    // âœ… Parse cleaned date string into Date object
                    const endDate = cleanDateStr
                      ? new Date(cleanDateStr)
                      : null;

                    // âœ… Compare with current date
                    const currentDate = new Date();
                    let status;

                    if (plan.name === "International Entrepreneur Academy") {
                      status = "Active";
                    } else {
                      status =
                        endDate && endDate >= currentDate
                          ? "Active"
                          : "Inactive";
                    }

                    return (
                      <div className="card shadow-sm border-0 mb-4" key={index}>
                        <div className="card-body">
                          <div className="d-flex justify-content-between align-items-center mb-3">
                            <h4>{plan.name}</h4>

                            {!plan.name.includes("Per Instance") && (
                              <span
                                className={`badge ${
                                  status === "Active"
                                    ? "bg-success"
                                    : "bg-secondary"
                                }`}
                              >
                                {status}
                              </span>
                            )}
                          </div>

                          <p className="text-muted">{plan.price}</p>

                          {!plan.name.includes("Per Instance") && (
                            <p>
                              <strong>Renewal Date:</strong> {plan.renewalDate}
                            </p>
                          )}

                          <p>
                            <strong>Last Payment:</strong> {plan.lastPayment}
                          </p>

                          <h6 className="mt-4">Features:</h6>
                          <ul>
                            {plan.features.map((feature, i) => (
                              <li key={i}>{feature}</li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </SectionWrapper>
        </div>
      </Wrapper>
    </>
  );
}
